import React from "react";
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { Slide, Grid, Button, withStyles, InputLabel, OutlinedInput, FormControl, Checkbox,  FormControlLabel, Typography } from '@material-ui/core';
import { ToggleButton, ToggleButtonGroup } from '@material-ui/lab'
import { Link as RouterLink } from "react-router-dom";
import customInputStyle from "../../assets/jss/material-kit-react/components/customInputStyle.jsx";
import loginStyle from "./authenticationStyle";
import SnackbarContent from "../uiComponent/Snackbar/SnackbarContent";
import logo from '../../assets/images/logo-circular.svg';
import history from '../../common/history';
import TextMaskCustom, { monthMaskFn, dateMaskFn } from "../common/MaskedInput";
import { Link } from "react-router-dom";

class CreateAccount extends React.Component {
    handleKeyDown = (e) => {
        if (e.key === 'Enter') {
            this.props.handleLogin(e);
        }
    }

    render() {
        console.log("VALID: ", validLoginFormFields, "INVALID", invalidLoginFormFields)
        const { classes, changeStep, handleChange, handleInput, handleToggle, formData, email, first_name, last_name, password, handleLogin, rememberMe, url, loginStatus, validLoginFormFields, invalidLoginFormFields, loginType } = this.props;
        const getInputClasses = (fieldName) => {
            return `custom-form-control colorless mini-control ${invalidLoginFormFields.includes(fieldName) ? 'error_class' :
                validLoginFormFields.includes(fieldName) ? 'success_class' : ''}`
        }
        const loginMessage = loginType === "listing" ? "Please login to view:" : loginType === 'referredListing' ? null : "See unmatched listings first:"
        console.log("MOBILE: ", formData)
        return (
            <Slide in={true} direction='left'>
                <div className={classes.authenticationRoot}>
                    <div className={classes.boxPanel}>
                        <Typography variant="h2" className={classes.introTitle}>{loginMessage}</Typography>
                        <div className={classes.shadowBox}>
                            <div className={classNames(classes.loginBox, classes.freeAccount)}>
                                { /* loginType && */
                                    <Link component='button' underline='none' onClick={() => changeStep('create account')}>
                                        <i className={`${classes.backArrow} sb-icon-keyboard_backspace`} /> BACK
                                </Link>
                                }
                                <div className={classes.cardLogo}>
                                    <img alt="logoimg" src={logo} />
                                </div>
                                <div className={classes.cardHeaederRed} >
                                    <Typography component='h2' variant="h5" color='textSecondary'>Free Account</Typography>
                                </div>
                                <div className={classes.cardHeaeder} >
                                    <Typography variant="body1" color='textSecondary'>See All Public listings after 21 days.</Typography>
                                </div>
                                <Grid container justify={'flex-start'} className={"space-reset "}>
                                    <input name='email' type='text' style={{ display: 'none' }}></input>
                                    <input name='password' type='password' style={{ display: 'none' }}></input>
                                    <Grid item xs={12} className={classes.cutomInputGrid}>
                                        <FormControl required variant="outlined" fullWidth className={getInputClasses("first_name")}>
                                            <InputLabel htmlFor="first_name">First Name</InputLabel>
                                            <OutlinedInput labelWidth={0} id="first_name" name="first_name" onChange={handleInput} onBlur={handleChange} onKeyDown={this.handleKeyDown} value={formData.first_name} />
                                            <i className={'sb-icon-user-outlined'}></i>
                                        </FormControl>
                                    </Grid>
                                    <Grid item xs={12} className={classes.cutomInputGrid}>
                                        <FormControl required variant="outlined" fullWidth className={getInputClasses("last_name")}>
                                            <InputLabel htmlFor="last_name">Last Name</InputLabel>
                                            <OutlinedInput labelWidth={0} id="last_name" name="last_name" onChange={handleInput} onBlur={handleChange} onKeyDown={this.handleKeyDown} value={formData.last_name} />
                                            <i className={'sb-icon-user-outlined'}></i>
                                        </FormControl>
                                    </Grid>
                                    <Grid item xs={12} className={classes.cutomInputGrid}>
                                        <FormControl required variant="outlined" fullWidth className={getInputClasses("email")}>
                                            <InputLabel htmlFor="email">Email</InputLabel>
                                            <OutlinedInput labelWidth={0} id="email" name="email" onChange={handleInput} onBlur={handleChange} onKeyDown={this.handleKeyDown} value={formData.email} />
                                            <i className={'sb-icon-envelope-alt'}></i>
                                        </FormControl>
                                    </Grid>
                                    <Grid item xs={12} className={classes.cutomInputGrid}>
                                        <FormControl required variant="outlined" fullWidth className={getInputClasses("password")}>
                                            <InputLabel htmlFor="password">Password</InputLabel>
                                            <OutlinedInput type="password" labelWidth={0} id="password" name="password" onChange={handleInput} onBlur={handleChange} onKeyDown={this.handleKeyDown} value={formData.password} />
                                            <i className={'sb-icon-lock'}></i>
                                        </FormControl>
                                    </Grid>
                                    <Grid item xs={12} className={classes.cutomInputGrid}>
                                        <FormControl required variant="outlined" fullWidth className={getInputClasses("mobile")}>
                                            <InputLabel htmlFor="mobile" /* shrink={formData.mobile && formData.mobile !== '' ? true : false} */>Mobile</InputLabel>
                                            <OutlinedInput
                                                type="text"
                                                labelWidth={0}
                                                inputComponent={TextMaskCustom}
                                                inputProps={{ mask: ['(', /[1-9]/, /\d/, /\d/, ')', ' ', /\d/, /\d/, /\d/, '-', /\d/, /\d/, /\d/, /\d/] }}
                                                id="mobile"
                                                name="mobile"
                                                onChange={handleInput}
                                                onBlur={handleChange}
                                                onKeyDown={this.handleKeyDown}
                                                value={formData.mobile}
                                            />
                                            <i className={'sb-icon-mobile'}></i>
                                        </FormControl>
                                    </Grid>
                                    <Grid item xs={12} sm={12} lg={12} className={classes.cutomInputGrid}>
                                        <Typography color="inherit" className="info-label" noWrap>DATE OF BIRTH</Typography>
                                        <div className={'smallGrid birthalign'}>
                                            <div className="col-as-3">
                                                <FormControl required variant="outlined" fullWidth className={getInputClasses("birth_month") + ' requiredInput smallInput'} >
                                                    <InputLabel htmlFor="birth_month" >MM</InputLabel>
                                                    <OutlinedInput inputComponent={TextMaskCustom} inputProps={{ mask: monthMaskFn }} value={formData.birth_month} labelWidth={0} id="birth_month" name="birth_month" onChange={handleInput} onBlur={handleChange} />
                                                </FormControl>
                                            </div>
                                            <div className="col-as-3">
                                                <FormControl required variant="outlined" fullWidth className={getInputClasses("birth_date") + ' requiredInput smallInput'}>
                                                    <InputLabel htmlFor="birth_date" >DD</InputLabel>
                                                    <OutlinedInput inputComponent={TextMaskCustom} inputProps={{ mask: dateMaskFn }} value={formData.birth_date} labelWidth={0} id="birth_date" name="birth_date" onChange={handleInput} onBlur={handleChange} />
                                                </FormControl>
                                            </div>
                                            <div className="col-as-6">
                                                <FormControl required variant="outlined" fullWidth className={getInputClasses("birth_year") + ' requiredInput smallInput'}>
                                                    <InputLabel htmlFor="birth_year" >YYYY</InputLabel>
                                                    <OutlinedInput inputComponent={TextMaskCustom} inputProps={{ mask: [/\d/, /\d/, /\d/, /\d/] }} value={formData.birth_year} labelWidth={0} id="birth_year" name="birth_year" onChange={handleInput} onBlur={handleChange} />
                                                </FormControl>
                                            </div>
                                        </div>
                                    </Grid>
                                </Grid>
                                <Grid item xs={12} sm={12} lg={12} className={classes.cutomInputGrid}>
                                    <Typography color="inherit" className="info-label" noWrap>GENDER</Typography>
                                    <div className={'smallGrid'}>
                                        <div className="col-as-12">
                                            <ToggleButtonGroup name='gender' id='gender' value={formData.gender} exclusive onChange={handleToggle} className="toggleCustom">
                                                <ToggleButton name='gender' id='gender' color="primary" value="male">MALE</ToggleButton>
                                                <ToggleButton name='gender' id='gender' color="primary" value="female">FEMALE</ToggleButton>
                                                {/* <ToggleButton name='gender' id='gender' color="primary" value="custom">CUSTOM</ToggleButton> */}
                                            </ToggleButtonGroup>
                                        </div>
                                    </div>
                                </Grid>
                                <Grid item xs={12} className={classes.cutomInputGrid}>
                                    <Button variant="contained" color='primary' fullWidth className={'redbtn'}>Sign Up</Button>
                                </Grid>
                            </div>
                        </div>
                    </div>
                    <div className={classNames(classes.boxFooter, classes.freeLogin, classes.m0)}>
                        Mistake? Got To:
                    <RouterLink to={`${process.env.PUBLIC_URL}/buyer-form`}>
                            <span>Premium</span>
                        </RouterLink>
                    </div>
                </div>
            </Slide>
        )
    }
}

CreateAccount.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(loginStyle, customInputStyle)(CreateAccount);