import axios from "axios";
import history from "../common/history";
import {BASE_URL} from "../common/config";

export const login = (userDetails) => {
    return (dispatch) => {
        return axios
            .post(`${BASE_URL}/login`, userDetails)
            .then(response => {
                document.cookie = `access_token=${[response.data.payload.token]}`
                history.push(`${process.env.PUBLIC_URL}/listing`);
                dispatch({ type: "LOGIN", payload: { message: response.statusText, failed: false } })
            })
            .catch(error => {
                if (error.response.status === 401) {
                    dispatch({ type: "LOGIN", payload: { message: error.response.statusText, failed: true } })
                }
            });
    };
}

export const submitEmail = (userDetails) => {
    return (dispatch) => {
        return axios
            .post(`${BASE_URL}/forgotpassword`, userDetails)
            .then(response => {
                dispatch({ type: "FORGOT_PASSWORD", payload: { message: response.statusText, failed: false } })
            })
            .catch(error => {
                if (error.response.status === 400) {
                    dispatch({ type: "FORGOT_PASSWORD", payload: { message: error.response.data.message, failed: true } })
                }
            });
    };
}

export const submitPassword = (userDetails) => {
    return (dispatch) => {
        return axios
            .post(`${BASE_URL}/resetpassword`, userDetails)
            .then(response => {
                dispatch({ type: "RESET_PASSWORD", payload: { message: response.statusText, failed: false } })
            })
            .catch(error => {
                // if (error.response.status === 400) {
                dispatch({ type: "RESET_PASSWORD", payload: { message: error.response.data.message, failed: true } })
                // }
            });
    };
}

export const logout = () => {
    return (dispatch) => {
        return axios
            .get(`${BASE_URL}/logout`)
            .then(response => {
                document.cookie = "access_token" + '=;expires=Thu, 01 Jan 1970 00:00:01 GMT;';
                const baseUrl = window.location.origin 
                ? window.location.origin + '/'
                : window.location.protocol + '/' + window.location.host + '/';
                window.location.href = baseUrl;
            })
            .catch(error => {
                // if (error.response.status === 401) {
                //     dispatch({ type: "LOGIN", payload: { message: error.response.statusText, failed: true } })
                // }
            });
    };
}