import React from 'react'
import ReactDOM from 'react-dom'
import { compose } from 'redux'
import { connect } from 'react-redux'
import classNames from "classnames";
import { withRouter } from 'react-router-dom';
import { withStyles } from '@material-ui/core/styles'
import { Modal as MUIModal, AppBar, Toolbar, List, ListItem, Hidden, IconButton } from '@material-ui/core'
import { Link } from 'react-router-dom';

import brand from "../../assets/images/logo-backdrop.svg";
import brandSmall from "../../assets/images/logo-circular.svg";
import { setLoginType } from '../../actions/property'

// core components
import Button from "../uiComponent/CustomButtons/Button";
import { AuthenticationContainer } from "../../components"
import { modalStyles } from './modalStyling' 


class Modal extends React.Component {
    constructor(props) {
        super(props)

        this.state = {
            modalOpen: false,
        }
    }

    componentDidMount() {
        this.props.setLoginType('listingModal')
    }

    open = () => {
        this.setState(prevState => ({
            modalOpen: true
        }))
    }

    close = () => {
        this.setState(prevState => ({
            modalOpen: false
        }))
    }


    render() {
        let { modalOpen } = this.state
        console.log("this.props.children: ", this.props.children);
        return (
             modalOpen ? 
                <ModalWrapper modalOpen={modalOpen} modalClose={this.close} > {this.props.children }</ModalWrapper>  : null
            
        )
        
    }
}

let ModalWrapper = (props) => {
    let { classes } = props;
    let color = 'transparent';
    let fixed = true;
    let absolute = false;

    const appBarClasses = classNames({
        [classes.appBar]: true,
        [classes[color]]: color,
        [classes.absolute]: absolute,
        [classes.fixed]: fixed
    });

    const brandComponent = <Link className={classes.title, classes.logo} to={`${process.env.PUBLIC_URL}/`}>
        <Hidden mdUp><img src={brandSmall} className={classes.logoSmall} alt={'Seller Bailout'} /></Hidden>
        <Hidden mdDown><img src={brand} className={classes.logo} alt={'Seller Bailout'} /></Hidden>
    </Link>;
    
    return (
        <MUIModal
            aria-labelledby="simple-modal-title"
            aria-describedby="simple-modal-description"
            open={props.modalOpen}
            BackdropProps={{
                classes: {
                    root: classes.backDrop
                }
            }}
            disableScrollLock={true}
            // container={document.getElementById('modalContainer')}
            className={classes.mainModalContainer}
            // disablePortal={true}
        // onClose={handleClose}
        >
            <div>
                <AppBar className={classNames(appBarClasses)}>
                    <Toolbar className={classes.container}>
                        <div className={classNames(classes.flex, classes.flexresponsive)}>
                            {brandComponent}
                        </div>
                        <Hidden smDown implementation="css">
                            <div className="navigation-wrapper">
                                <Hidden mdDown>
                                    <List className={classes.list}>
                                        {/* ******* User not logged in ********* */}
                                        <ListItem className={classes.listItem}>
                                            <Button color="transparent" variant='outlined' disableRipple component='span' onClick={props.modalClose} className={classNames(classes.navLink, classes.whiteLinkButton)}>
                                                Listings
                                            </Button>
                                        </ListItem>
                                    </List>
                                </Hidden>
                            </div>
                        </Hidden>
                    </Toolbar>
                    <Button color='transparent' disableRipple component='span' onClick={props.modalClose} className={classNames(classes.closeButton)} />
                </AppBar>
                <div className={classes.marginmodaltop}>
                    {props.children}
                </div>
            </div>
            {/* <h2 id="simple-modal-title">Text in a modal</h2>
            <p id="simple-modal-description">
                Duis mollis, est non commodo luctus, nisi erat porttitor ligula.
            </p> */}
        </MUIModal>
    )
}

ModalWrapper = withStyles(modalStyles)(ModalWrapper)

export default connect(null, { setLoginType }, null, { withRef: true })(withRouter(Modal))