import React from 'react';
import compose from 'recompose/compose'
import { connect } from "react-redux";
import { bindActionCreators } from "redux";

import { withStyles } from '@material-ui/core/styles';
import PropTypes from 'prop-types';
import { Grid, Stepper, Step, StepLabel, StepConnector, Typography } from '@material-ui/core';
import Button from "../uiComponent/CustomButtons/Button";
import { staticData } from "../../common";
import classNames from 'classnames';
import { BuyerPlan, BuyerInfo, ProfileType, BuyerCheckout, BuyerSucess } from "../buyer";
import { validateBuyerFormData, validateBuyerInfo, validateBuyerProfile } from './validation';
import SnackbarContent from "../uiComponent/Snackbar/SnackbarContent";
import {buyerActions} from '../../actions';  
import buyerStyle from "./buyerStyle";
import { sanitizeText } from '../common/utils';

function getStepContent(step, handleChange, formData, invalidBuyerFormFields, handleInput, handlButtonToggle, validBuyerFormFields, handleDoneClick_buyerInfo, handleDoneClick_buyerProfile, setBuyerInfoValidated, handleFormEdit, handleNext, setBuyerProfileValidated, submitBuyerFormData, getSectionCompleted) {
  switch (step) {
    case 0:
      return (
        <BuyerPlan handleChange={handleChange} handleInput={handleInput}
          formData={formData} invalidBuyerFormFields={invalidBuyerFormFields} />
      );
    case 1:
      return (
        <BuyerInfo handleChange={handleChange} handleInput={handleInput} handlButtonToggle={handlButtonToggle}
          formData={formData} invalidBuyerFormFields={invalidBuyerFormFields} validBuyerFormFields={validBuyerFormFields}
          handleDoneClick_buyerInfo={handleDoneClick_buyerInfo} setBuyerInfoValidated={setBuyerInfoValidated} getSectionCompleted={getSectionCompleted} />
      );
    case 2:
      return (
        <ProfileType handleChange={handleChange} handleInput={handleInput}
          formData={formData} invalidBuyerFormFields={invalidBuyerFormFields} validBuyerFormFields={validBuyerFormFields}
          setBuyerProfileValidated={setBuyerProfileValidated} handleDoneClick_buyerProfile={handleDoneClick_buyerProfile} getSectionCompleted={getSectionCompleted} />
      );
    case 3:
      return (
        <BuyerCheckout handleChange={handleChange}
          formData={formData} invalidBuyerFormFields={invalidBuyerFormFields} handleFormEdit={handleFormEdit} handleNext={handleNext} submitBuyerFormData={submitBuyerFormData} />
      );
    default:
      return (
        <BuyerSucess />
      );
  }
}

class BuyerForm extends React.Component {
  state = {
    activeStep: 0,
    skipped: new Set(),
    buyerFormData: staticData.buyerFormData,
    completedSteps: [],
    invalidBuyerFormFields: [],
    validBuyerFormFields: [],
    buyerInfoValidated: false,
    buyerProfileValidated: false,
    validatedSections: [],
  };

  isStepOptional = step => {
    return step === 1;
  };

  handleNext = () => {
    console.log("NEXT")
    let { activeStep, completedSteps, invalidBuyerFormFields } = this.state;
    const invalidFields = validateBuyerFormData(activeStep, this.state.buyerFormData);
    console.log("INVALID FIELDS: ", invalidFields)
    invalidBuyerFormFields = Object.assign(invalidBuyerFormFields, invalidFields);
    if (invalidBuyerFormFields.length) {
      console.log("RETURN")
      this.setState({ invalidBuyerFormFields });
      return;
    }
    completedSteps.push(activeStep);
    let { skipped } = this.state;
    if (this.isStepSkipped(activeStep)) {
      skipped = new Set(skipped.values());
      skipped.delete(activeStep);
    }
    this.setState({
      activeStep: activeStep + 1,
      skipped,
      completedSteps
    });
    window.scrollTo(0, 0);

  };

  handleDoneClick_buyerInfo = (stepName) => {
    let { invalidBuyerFormFields } = this.state;
    const invalidFields = validateBuyerInfo(stepName, this.state.buyerFormData);
    invalidBuyerFormFields = Object.assign(invalidBuyerFormFields, invalidFields);
    if (invalidBuyerFormFields.length) {
      this.setState({ invalidBuyerFormFields });
      return;
    }
    return true;
  };

  handleDoneClick_buyerProfile = (stepName) => {
    let { invalidBuyerFormFields } = this.state;
    const invalidFields = validateBuyerProfile(stepName, this.state.buyerFormData);
    invalidBuyerFormFields = Object.assign(invalidBuyerFormFields, invalidFields);
    if (invalidBuyerFormFields.length) {
      this.setState({ invalidBuyerFormFields });
      return;
    }
    return true;
  }

  setBuyerInfoValidated = () => {
    this.setState({ buyerInfoValidated: true })
  }

  setBuyerProfileValidated = () => {
    this.setState({ buyerProfileValidated: true })
  };

  handlButtonToggle = (event, value) => {
    if (value === "male" || value === "female") {
      this.setState({
        buyerFormData: {
          ...this.state.buyerFormData, "gender": value,
        },
        validBuyerFormFields: this.state.validBuyerFormFields.includes("gender") ? [
          ...this.state.validBuyerFormFields,
        ] : [
          ...this.state.validBuyerFormFields,
          "gender",
        ]
      });
    } else if (value === "yes" || value === "no") {
      this.setState({
        buyerFormData: {
          ...this.state.buyerFormData, "also_mailing_address": value,
        },
        validBuyerFormFields: this.state.validBuyerFormFields.includes("also_mailing_address") ? [
          ...this.state.validBuyerFormFields,
        ] : [
          ...this.state.validBuyerFormFields,
          "also_mailing_address",
        ]
      },() => console.log(this.state));
    }
  }

  handleBack = () => {
    const { activeStep, completedSteps } = this.state;
    completedSteps.splice(completedSteps.indexOf(activeStep - 1))
    this.setState(state => ({
      activeStep: state.activeStep - 1,
      completedSteps,
      invalidBuyerFormFields: []
    }));
    window.scrollTo(0, 0);
  };

  handleReset = () => {
    this.setState({
      activeStep: 0,
      completedSteps: []
    });
  };

  isStepSkipped(step) {
    return this.state.skipped.has(step);
  }

  handleInput = event => {
    console.log("CHANGE $$$$$$> ", event.target)
    if ((event.target.name === "zip" || event.target.name === "cvc" || event.target.name === "card-number"
      || event.target.name === "birth-month" || event.target.name === "birth-date" || event.target.name === "birth-year") && (isNaN(event.target.value))) {
      return;
    }
    this.setState({
      buyerFormData: {
        ...this.state.buyerFormData, [event.target.name]: event.target.value,
      },
    });
  }

  handleChange = (event) => {
    let name = event.target.name
    let value = sanitizeText(name, event.target.value)

      console.log("CHANGE =====> ", event.target, name, value)

    let { invalidBuyerFormFields, validBuyerFormFields } = this.state;
    if (!name && !value) {
      if (event.currentTarget.attributes.name.value && event.currentTarget.attributes.value.value) {
        this.setState({
          buyerFormData: {
            ...this.state.buyerFormData, [event.currentTarget.attributes.name.value]: event.currentTarget.attributes.value.value
          },
        });

      }
    }
    else {
      this.setState({
        buyerFormData: {
          ...this.state.buyerFormData, [name]: value,
        },
      }, () => console.log(this.state));
    }
    if (value === "" || value === null) {
      if (!invalidBuyerFormFields.includes(name)) {
        invalidBuyerFormFields.push(name);
        const index = validBuyerFormFields.indexOf(name);
        if (validBuyerFormFields.includes(name)) {
          validBuyerFormFields.splice(index, 1);
        }
      }
    }
    else {
      const index = invalidBuyerFormFields.indexOf(name);
      if (invalidBuyerFormFields.includes(name)) {
        invalidBuyerFormFields.splice(index, 1);
      }
      if (!validBuyerFormFields.includes(name)) {
        validBuyerFormFields.push(name);
      }
    }
    if (name === "first_name" || name === "last_name") {
      this.setState(prevState => ({
        buyerFormData: {
          ...prevState.buyerFormData,
          [name]: value,
        }
      }), console.log("STATE: ", this.state))
    }
    if (name === "email" && value !== "") {
      if (!this.validateEmail(value)) {
        if (!invalidBuyerFormFields.includes(name)) {
          invalidBuyerFormFields.push(name);
          const index = validBuyerFormFields.indexOf(name);
          if (validBuyerFormFields.includes(name)) {
            validBuyerFormFields.splice(index, 1);
          }
        }
      }
      else {
        const index = invalidBuyerFormFields.indexOf(name);
        if (invalidBuyerFormFields.includes(name)) {
          invalidBuyerFormFields.splice(index, 1);
        }
        if (!validBuyerFormFields.includes(name)) {
          validBuyerFormFields.push(name);
        }
      }
    }
    if (name === "zip" && value !== "") {
      if (!this.validateZip(value)) {
        if (!invalidBuyerFormFields.includes(name)) {
          invalidBuyerFormFields.push(name);
          const index = validBuyerFormFields.indexOf(name);
          if (validBuyerFormFields.includes(name)) {
            validBuyerFormFields.splice(index, 1);
          }
        }
      }
      else {
        const index = invalidBuyerFormFields.indexOf(name);
        if (invalidBuyerFormFields.includes(name)) {
          invalidBuyerFormFields.splice(index, 1);
        }
        if (!validBuyerFormFields.includes(name)) {
          validBuyerFormFields.push(name);
        }
      }
    }
    if (name === "cvc" && value !== "") {
      if (!this.validateCvc(value)) {
        if (!invalidBuyerFormFields.includes(name)) {
          invalidBuyerFormFields.push(name);
          const index = validBuyerFormFields.indexOf(name);
          if (validBuyerFormFields.includes(name)) {
            validBuyerFormFields.splice(index, 1);
          }
        }
      }
      else {
        const index = invalidBuyerFormFields.indexOf(name);
        if (invalidBuyerFormFields.includes(name)) {
          invalidBuyerFormFields.splice(index, 1);
        }
        if (!validBuyerFormFields.includes(name)) {
          validBuyerFormFields.push(name);
        }
      }
    }
    if ((name === "work_phone" || name === "phone") &&
      value !== "") {
      const number = value.replace(/[^0-9.]/g, "");
      if (number.toString().length < 10) {
        if (!invalidBuyerFormFields.includes(name)) {
          invalidBuyerFormFields.push(name);
          const index = validBuyerFormFields.indexOf(name);
          if (validBuyerFormFields.includes(name)) {
            validBuyerFormFields.splice(index, 1);
          }
        }
      }
      else {
        const index = invalidBuyerFormFields.indexOf(name);
        if (invalidBuyerFormFields.includes(name)) {
          invalidBuyerFormFields.splice(index, 1);
        }
        if (!validBuyerFormFields.includes(name)) {
          validBuyerFormFields.push(name);
        }
      }
    }
    if (name === "password_confirmation" && value !== "") {
      if (!this.matchPassword(value)) {
        if (!invalidBuyerFormFields.includes(name)) {
          invalidBuyerFormFields.push(name);
          const index = validBuyerFormFields.indexOf(name);
          if (validBuyerFormFields.includes(name)) {
            validBuyerFormFields.splice(index, 1);
          }
        }
      }
      else {
        const index = invalidBuyerFormFields.indexOf(name);
        if (invalidBuyerFormFields.includes(name)) {
          invalidBuyerFormFields.splice(index, 1);
        }
        if (!validBuyerFormFields.includes(name)) {
          validBuyerFormFields.push(name);
        }
      }
    }
    this.setState({
      invalidBuyerFormFields: invalidBuyerFormFields,
      validBuyerFormFields: validBuyerFormFields
    });
  };

  handleFormEdit = (stepIndex) => {
    this.setState({ activeStep: stepIndex })
  };

  validateEmail(value) {
    const emailRegex = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return emailRegex.test(String(value).toLowerCase());
  };

  matchPassword(value) {
    return value === this.state.buyerFormData.password;
  }

  validateZip(value) {
    const zipRegex = /^[0-9]*$/;
    return zipRegex.test(String(value).toLowerCase()) && value.length === 5;
  };

  validateCvc(value) {
    const cvcRegex = /^[0-9]{3,4}$/;
    console.log("CVC: ", String(value).trim())

    return cvcRegex.test(String(value).trim());
  };

  validateDateMonth(value) {
    const cvcRegex = /^[0-9]{2}$/;
    return cvcRegex.test(String(value).toLowerCase());
  };
  validateYear(value) {
    const cvcRegex = /^[0-9]{3,4}$/;
    return cvcRegex.test(String(value).toLowerCase());
  };

  getStepIcon = (step, stepIndex) => {
    const icon1 = <i className="sb-icon-plan"></i>
    const icon2 = <i className="sb-icon-buyer"></i>
    const icon3 = <i className="sb-icon-user-check"></i>
    const icon4 = <i className="sb-icon-send"></i>
    const stepCompletedIcon = <i className="sb-icon-tick-bold"></i>

    if (this.state.completedSteps.indexOf(stepIndex) > -1) {
      return stepCompletedIcon;
    }

    switch (step.step) {
      case "PLAN": return icon1;
      case "BUYER": return icon2;
      case "PROFILE": return icon3;
      case "CONFIRM": return icon4;
      default:
    }
  };

  isValidStep = () => {
    if (this.state.activeStep === 0) {
      return this.state.buyerFormData.plan;
    }
    else if(this.state.activeStep === 1){
      return this.getSectionCompleted('buyerInfoAll')
    }
    else if(this.state.activeStep === 2){
      return this.getSectionCompleted('profileTypeAll')
    }
    else if(this.state.activeStep === 3){
      return true
    }
  };

  submitBuyerFormData = () => {
    this.props.registerBuyer(this.state.formData);
  }

  getSectionCompleted = (section) => {
    let { invalidBuyerFormFields, validBuyerFormFields } = this.state
    let found
    let buyerLoginFields = ['email', 'password', 'password_confirmation']
    let buyerInfoFields = ['first_name', 'last_name', 'work_phone', 'phone', 'birth-month', 'birth-date', 'birth-year', 'gender']
    let billingAddressFields = ['address', 'city', 'state', 'zip', 'country', 'also_mailing_address']
    let paymentInfoFields = ['card-type', 'card-number', 'name-on-card', 'exp-month', 'exp-year', 'cvc']
    let profileTypeFields = ['profile_type_id']
    let investingExperienceFields = ['purchase', 'are_you_cash_buyer', 'closing_period']
    let generalInformationFields = ['buy_in', 'are_you_agent', 'heard']

    if( invalidBuyerFormFields.length <= 0 && validBuyerFormFields.length <= 0 ){
      return true
    }

    switch(section){
      case 'buyerLogin':
        found = buyerLoginFields.some(item => !invalidBuyerFormFields.includes(item)) && buyerLoginFields.every(item => validBuyerFormFields.includes(item))
        return !found
      case 'buyerInfo':
        found = buyerInfoFields.some(item => !invalidBuyerFormFields.includes(item)) && buyerInfoFields.every(item => validBuyerFormFields.includes(item))
        return !found
      case 'billingAddress':
        found = billingAddressFields.some(item => !invalidBuyerFormFields.includes(item)) && billingAddressFields.every(item => validBuyerFormFields.includes(item))
        return !found
      case 'paymentInfo':
        found = paymentInfoFields.some(item => !invalidBuyerFormFields.includes(item)) && paymentInfoFields.every(item => validBuyerFormFields.includes(item))
        return !found
      case 'buyerInfoAll':
        found = [
          ...buyerLoginFields,
          ...buyerInfoFields,
          ...billingAddressFields,
          ...paymentInfoFields,
        ].some(item => !invalidBuyerFormFields.includes(item)) &&
        [
          ...buyerLoginFields,
          ...buyerInfoFields,
          ...billingAddressFields,
          ...paymentInfoFields,
        ].every(item => validBuyerFormFields.includes(item)) 

        return found
      case 'profileType':
        found = profileTypeFields.some(item => !invalidBuyerFormFields.includes(item)) && profileTypeFields.every(item => validBuyerFormFields.includes(item))
        return !found
      case 'investingExperience':
        found = investingExperienceFields.some(item => !invalidBuyerFormFields.includes(item)) && investingExperienceFields.every(item => validBuyerFormFields.includes(item))
        return !found
      case 'generalInfo':
        found = generalInformationFields.some(item => !invalidBuyerFormFields.includes(item)) && generalInformationFields.every(item => validBuyerFormFields.includes(item))
        return !found
      case 'profileTypeAll':
        found = [
          ...profileTypeFields,
          ...investingExperienceFields,
          ...generalInformationFields,
        ].some(item => !invalidBuyerFormFields.includes(item)) &&
        [
          ...profileTypeFields,
          ...investingExperienceFields,
          ...generalInformationFields,
        ].every(item => validBuyerFormFields.includes(item)) 

        return found
      default:
        return true
    }
  }

  render() {
    const steps = staticData.buyerFormSteps;
    // let SB_IsMobileView = window.matchMedia("(max-width: 767px)").matches;

    const { activeStep, buyerFormData, invalidBuyerFormFields } = this.state;
    const { classes } = this.props;
    const connector = (
      <StepConnector
        classes={{
          active: classes.connectorActive,
          completed: classes.connectorCompleted,
          disabled: classes.connectorDisabled,
          line: classes.connectorLine,
        }}
      />
    );

    return (
      <Grid className={classes.container} container direction="row" justify="center" alignItems="flex-start">
        <Grid item xs={12} className={'form-wrapper'} style={{ paddingBottom: "80px" }}>
          <div className={classes.stepperHead}>
            <Typography variant="h5" align='center'>Buyer Form.</Typography>
            <Typography variant="h6" align='center'>See Off-Market opportunities here first.</Typography>
            <Stepper activeStep={activeStep} className={classes.stepper} alternativeLabel connector={connector}>
              {steps.map((step, index) => {
                const props = {};
                const labelProps = {};
                return (
                  <Step key={step} {...props}>
                    <StepLabel
                      className={classes.stepperLabel}
                      classes={{
                        active: classes.stepperActive,
                        completed: classes.stepperCompleted,
                        disabled: classes.stepperDisabled,
                        line: classes.stepperLine,
                      }}
                      icon={this.getStepIcon(step, index)}{...labelProps}>{step.step}
                    </StepLabel>
                  </Step>
                );
              })}
            </Stepper>
          </div>

          <Grid item xs={12}>
            {activeStep === steps.length ? (
              <Grid item xs={12}>
                <BuyerSucess />
              </Grid>
            ) : (
                <Grid item xs={12}>
                  <Grid xs={12} item>
                    {getStepContent(activeStep, this.handleChange, buyerFormData, invalidBuyerFormFields, this.handleInput,
                      this.handlButtonToggle, this.state.validBuyerFormFields, this.handleDoneClick_buyerInfo, this.handleDoneClick_buyerProfile,
                      this.setBuyerInfoValidated, this.handleFormEdit, this.handleNext, this.setBuyerProfileValidated, this.submitBuyerFormData, this.getSectionCompleted)}
                  </Grid>

                  <Grid
                    container
                    direction="row"
                    justify={`${window.SB_IsMobileView ? (activeStep === 0 ? "center" : "space-between")
                      : (activeStep === 0 ? "flex-end" : "space-between")}`}
                    alignItems="center"
                    className={classes.buyerFormBtnGrid}
                  >
                    {activeStep !== 0 && (
                      <Button
                        simple
                        onClick={this.handleBack}
                        className={classNames(classes.buttonSecondryTrans)}
                      >
                        Back
                    </Button>)}
                    {
                      activeStep !== steps.length &&
                      <Button
                        color="primary"
                        size="lg"
                        disabled={!this.isValidStep()}
                        onClick={this.handleNext}
                        className={classNames(classes.buttonSecondry)}
                      >
                        {activeStep === steps.length - 1 ? 'Confirm' : 'Next'}
                      </Button>
                    }
                  </Grid>
                  <Grid
                    container
                    direction="row"
                    justify='center'
                    alignItems="center"
                    className={classNames(classes.notification)}
                  >
                    <SnackbarContent
                      message={
                        <span>Every week we match highly actionable Buyers to Sellers before listings make it on the market.</span>
                      }
                      close
                      color="warning"
                      icon="sb-icon-info"
                    />
                  </Grid>
                </Grid>
              )}
          </Grid>
        </Grid>
      </Grid>

    );
  }
}

BuyerForm.propTypes = {
  classes: PropTypes.object.isRequired,
};

const mapStateToProps = (state) => {
  return {}
};

const mapDispatchToProps = (dispatch) => {
  return {
    ...bindActionCreators(buyerActions, dispatch)
  }
};

export default compose(
  withStyles(buyerStyle),
  connect(mapStateToProps, mapDispatchToProps)
)(BuyerForm);