export function sanitizeText(name, value){
    if(
        name === 'field-first-name' ||
        name === 'field-last-name' ||
        name === 'first_name' ||
        name === 'last_name' ||
        name === 'job_title' ||
        name === 'name-on-card'
    ){
        let temp = value.split(' ')
        temp = temp.map(value => value.charAt(0).toUpperCase() + value.slice(1).toLowerCase())
        let sanitized = temp.join(' ')
        return sanitized
    }
    else if(
        name === 'birth-month' ||
        name === 'birth-date'
    ){
        if(value.trim().length == 1){
            return `0${value}`
        }
        else{
            return value.trim()
        }
    }
    else if(name === 'birth-year'){
        let year = parseInt(value)
        let currentYear = new Date().getFullYear()

        if(year < 1900){
            return 1900
        }
        else if(year > currentYear){
            return currentYear
        }
    }
    else{
        return value
    }
}

export function getReferrer(referrer){
    return referrer.match(/:\/\/(.[^/]+)/) ? referrer.match(/:\/\/(.[^/]+)/)[1] : ''
}