import React, { useRef } from 'react'
import classNames from 'classnames'
import { withStyles } from '@material-ui/core/styles';
import { Typography, Button } from '@material-ui/core';
import CreateAccountModal from "../common/Modal.jsx";

const styles = theme => ({
    mainContainer: {
        position: 'absolute',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'center',
        top: 0,
        left: 0,
        zIndex: 999,
        backgroundColor: 'rgba(0, 0, 0, 0.8)',
        width: '100%',
        height: '100%',
    },
    dropdown: {
        '& .heading': {
            color: 'white',
            fontSize: '12px',
            fontWeight: 500,
            // fontSize: '90%',
            marginBottom: 20,
            [theme.breakpoints.down("sm")]: {
                fontSize: '13px',
            },
        },
        '& .button': {
            width: 120,
            height: 33,
            backgroundColor: '#a7a7a7',
            color: 'white',
            fontWeight: 'Bold',
            fontSize: 10,
            [theme.breakpoints.down("sm")]: {
                fontSize: '13px',
                width: 140,

            },
            '&:hover': {
                backgroundColor: '#0584de',
            }
        },
    },
    imgBillboard: {
        '& .heading': {
            color: 'white',
            fontSize: '48px',
            fontWeight: 500,
            // fontSize: '90%',
            marginBottom: 12,
        },
        '& .subheading': {
            color: 'white',
            fontSize: '26px',
            marginBottom: 24,
        },
        '& .button': {
            width: 260,
            height: 54,
            backgroundColor: '#a7a7a7',
            color: 'white',
            fontWeight: 'Bold',
            fontSize: 16,
            '&:hover': {
                backgroundColor: '#0584de',
            }
        }
    },
})

const CreateAccountOverlay = (props) => {
    const classes = props.classes
    let createAccountModal = useRef(null);

    const onButtonClick = () => {
        // `current` points to the mounted text input element
        // inputEl.current.focus();
        console.log("Create Account Modal: ", createAccountModal)
        createAccountModal.open()
    };

    if (props.variant === 'dropdown') {
        return (
            <div className={classNames(classes.mainContainer, classes.dropdown)}>
                <Typography className={'heading'}>To Access Listings:</Typography>
                <Button className={'button'} onClick={onButtonClick}>Create Account</Button>

                <CreateAccountModal wrappedComponentRef={c => (createAccountModal = c)}>{props.children}</CreateAccountModal>
            </div>
        )
    }
    else if (props.variant === 'imgBillboard') {
        return (
            <div className={classNames(classes.mainContainer, classes.imgBillboard)}>
                <Typography variant='h4' className={'heading'}>Interested?</Typography>
                <Typography variant='h5' className={'subheading'}>To see this listing, please:</Typography>
                <Button className={'button'} onClick={onButtonClick}>Create Account</Button>

                <CreateAccountModal wrappedComponentRef={c => (createAccountModal = c)}>{props.children}</CreateAccountModal>
            </div>
        )
    }
}

export default withStyles(styles)(CreateAccountOverlay)