import React from "react";
import PropTypes from 'prop-types';
import { withRouter } from 'react-router-dom'
import { Grid, Button, Link, withStyles, InputLabel, OutlinedInput, FormControl, Checkbox, FormControlLabel, Typography } from '@material-ui/core';
import { Link as RouterLink } from "react-router-dom";
import customInputStyle from "../../assets/jss/material-kit-react/components/customInputStyle.jsx";
import loginStyle from "./authenticationStyle";
import SnackbarContent from "../uiComponent/Snackbar/SnackbarContent";
import classNames from 'classnames';
import logo from '../../assets/images/logo-circular.svg'
import history from '../../common/history';
import CreateAccountPlans from "./createAccountPlans.jsx";
import FreeAccount from "./freeAccount.jsx";

class CreateAccount extends React.Component {
    state = {
        loginType: null,
        screen: 'create account',
    }

    changeStep = (step) => {
        this.setState(prevState => ({
            screen: step,
        }))
    }

    getComponent = () => {

        switch (this.state.screen) {
            case 'create account':
                return <CreateAccountPlans classes={this.props.classes} changeStep={this.changeStep} loginType={this.props.loginType} />
            case 'free account form':
                return <FreeAccount {...this.props} changeStep={this.changeStep} />
            default:
                return null
        }
    }

    render() {
        const { classes, handleChange, handleInput, email, password, handleLogin, rememberMe, url, loginStatus, validLoginFormFields, invalidLoginFormFields, loginType, match } = this.props;
        const { screen } = this.state
        const getInputClasses = (fieldName) => {
            return `custom-form-control mini-control required ${classes.requiredInput} ${invalidLoginFormFields.includes(fieldName) ? 'error_class' :
                validLoginFormFields.includes(fieldName) ? 'success_class' : ''}`
        }
        const loginMessage = loginType === "listing" ? "Please login to view:" : "See unmatched listings first:"

        return (
            <div >
                {this.getComponent()}
            </div >
        )
    }
}

CreateAccount.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(loginStyle, customInputStyle)(withRouter(CreateAccount));