import { combineReducers } from "redux";
import { sellerReducer, sellerFormDataReducer, authenticateReducer, propertyReducer, myAccountReducer } from "../reducers";

const rootReducer = combineReducers({
    sellerReducer,
    sellerFormDataReducer,
    authenticateReducer,
    propertyReducer,
    myAccountReducer
})

export default rootReducer;
