class ReferredPropertyDetail extends React.Component {
    state = {
        fetchingPropertyDetails: false,
    }

    componentDidMount() {
        this.props.setLoginType('referredListing')
        this.props.fetchPropertyDetails(this.props.match.params.id);
        this.props.getBuyerProfileDetails();
        this.setState({
            fetchingPropertyDetails: true
        });
    }
    componentWillReceiveProps(nextProps) {
        nextProps.propertyDetails && this.setState({ fetchingPropertyDetails: false })
    }

    render() {
        let { classes, propertyDetails, getRestrictionClass } = this.props
        console.log("+++++======++++++=============+++++++============> ", this.props.propertyDetails)

        if (propertyDetails) {
            return (
                <div className={classes.mainContainer}>
                    <Typography variant='h4' className={classes.sectionTitle}>To learn more about:</Typography>
                    <Typography variant='h4' className={classNames(classes.sectionTitle, classes.sectionTitle2)}>Please login:</Typography>

                    <div className={classes.propertyRoot}>
                        <div className={classes.propertyContainer}>
                            <div className={classes.titleInfo}>
                                <div className={classes.propertyTitle}>
                                    <Typography variant="h2">{propertyDetails.street1}{propertyDetails.street2 && `, #${propertyDetails.street2}`}</Typography>
                                    <Typography variant="h4">{propertyDetails.city}, {propertyDetails.state} {propertyDetails.zip}</Typography>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className={classNames(classes.propertyImgContainer, classes.shadowBox)}>
                        <img src={`${MEDIA_STORAGE_URL}/${propertyDetails.photos.length > 0 && propertyDetails.photos[0].file_name}`} className={classes.propertyImg} />
                    </div>

                    <div className={classes.descContainer}>
                        <h5 className={classes.descHeading}>Description</h5>
                        <p className={classes.descBody}>{propertyDetails.property_description}</p>
                    </div>

                    <div className={classes.infoBox}>
                        <SnackbarContent
                            message={
                                <div>
                                    <div>This listing is for premium members:</div>
                                    <b className={classes.alertBold}>First-Mover Investors.</b>
                                    <div>Please Login, Create a Member <br /> Account and/or Change your Plan <br />if you like to access this listing.</div>
                                </div>
                            }
                            close
                            color="gray"
                            icon="sb-icon-info"
                            className="gray"
                            icons="top"
                        />
                    </div>
                    <div className={classes.form}>
                        <AuthenticationContainer />
                    </div>
                    <Button color='primary' variant='contained' className={classes.loginButton}>Go to login</Button>
                </div>
            )
        }
        else {
            return null
        }
    }
}

const mapStateToProps = (state) => {
    return {
        ...state.propertyReducer,
        ...state.myAccountReducer
    }
};

const mapDispatchToProps = (dispatch) => {
    return {
        ...bindActionCreators({ ...propertyActions, ...myAcccountActions, setLoginType }, dispatch)
    }
};
export default compose(
    connect(mapStateToProps, mapDispatchToProps),
    withStyles(styles),
)(AccountTypeHOC_F(ReferredPropertyDetail))