import React from "react";
import PropTypes from 'prop-types';
import { withRouter } from 'react-router-dom'
import { Grid, Button, Link, withStyles, InputLabel, OutlinedInput, FormControl, Checkbox, FormControlLabel, Typography } from '@material-ui/core';
import { Link as RouterLink } from "react-router-dom";
import customInputStyle from "../../assets/jss/material-kit-react/components/customInputStyle.jsx";
import loginStyle from "./authenticationStyle";
import SnackbarContent from "../uiComponent/Snackbar/SnackbarContent";
import classNames from 'classnames';
import logo from '../../assets/images/logo-circular.svg'
import history from '../../common/history'

class AccountVerification extends React.Component {
    state = {
        loginType: null
    }

    render() {
        const { classes, handleChange, handleInput, email, password, handleLogin, rememberMe, url, loginStatus, validLoginFormFields, invalidLoginFormFields, loginType, match } = this.props;
        const getInputClasses = (fieldName) => {
            return `custom-form-control colorless mini-control required ${classes.requiredInput} ${invalidLoginFormFields.includes(fieldName) ? 'error_class' :
                validLoginFormFields.includes(fieldName) ? 'success_class' : ''}`
        }
        const loginMessage = "Please login to view:"

        return (
            <div className={classes.authenticationRoot}>
                <div className={classes.boxPanel}>
                    <Typography variant="h2" className={classes.introTitle}>{loginMessage}</Typography>
                    <div className={classes.shadowBox}>
                        <div className={classNames(classes.loginBox, classes.freeAccount)}>
                            { /* loginType && */
                                <Link component='button' underline='none' onClick={() => { history.goBack() }}>
                                    <i className={`${classes.backArrow} sb-icon-keyboard_backspace`} /> BACK
                                </Link>
                            }
                            <div className={classes.cardLogo}>
                                <img src={logo} />
                            </div>
                            <div className={classes.cardHeaeder} >
                                <Typography component='h2' variant="h5" color='textSecondary'>Free Account</Typography>
                            </div>
                            <div className={classes.cardHeaeder} >
                                <Typography variant="body1" color='textSecondary' gutterBottom paragraph>An email was sent to you <br /> with a one-time login code.</Typography>
                            </div>
                            <Grid container justify={'flex-start'} className={"space-reset "}>
                                <Grid item xs={12} className={classes.cutomInputGrid}>
                                    <FormControl required variant="outlined" fullWidth className={getInputClasses("first_name")}>
                                        <InputLabel htmlFor="component-filled">Validate Code Here</InputLabel>
                                        <OutlinedInput labelWidth={0} id="email" name="first_name" onChange={handleInput} onBlur={handleChange} onKeyDown={this.handleKeyDown} value={''} />
                                        <i className={'sb-icon-checkmark-outline'}></i>
                                    </FormControl>
                                </Grid>
                                <Grid item xs={12} className={classes.cutomInputGrid}>
                                    <Button variant="contained" color='primary' fullWidth className={'redbtn'}>Sign In</Button>
                                </Grid>
                            </Grid>
                            <div className={classes.cardHeaeder} className={classes.titleMarginTop}>
                                <Typography variant="body1" color='textSecondary'>Didn't Receive?</Typography>
                            </div>
                            <Grid item xs={12} sm={12} md={12} lg={12} className={classes.cutomInputGrid}>
                                <Button variant="contained" color='default' className={'graybtn'}>Resend Code</Button>
                            </Grid>
                        </div>
                    </div>
                </div>
                <div className={classNames(classes.boxFooter, classes.freeLogin)}>
                    Mistake? Got To:
                    <RouterLink to={`${process.env.PUBLIC_URL}/buyer-form`}>
                        <span>Premium</span>
                    </RouterLink>
                </div>
            </div>
        )
    }
}

AccountVerification.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(loginStyle, customInputStyle)(withRouter(AccountVerification));