class ComponentWithAccountType extends React.Component{
    constructor(props){
        super(props)
    }

    getAccountType = () => {
        return this.props.accountType
    }

    getRestrictionClass = (classname) => {
        const classes = this.props.classes
        
        if(this.props.accountType === 'public'){
            return classes[classname]
        }
        else{
            return ''
        }
    }

    getDisabled = (condition) => {
        if(typeof condition === 'string')
            return this.props.accountType === condition
        else if(Array.isArray(condition))
            return condition.includes(this.props.accountType)
    }

    render(){
        console.log("ACCOUNT", this.getAccountType())
        const children = React.Children.map(this.props.children, (child, index) => {
            return React.cloneElement(child, {
                accountType: this.getAccountType(),
                getRestrictionClass: this.getRestrictionClass
            });
        });

        return (
            <React.Fragment>
                { children }
            </React.Fragment>
        )
    }

}


const mapStateToProps = (state) => ({
    accountType: state.myAccountReducer.buyerProfileDetails ? state.myAccountReducer.buyerProfileDetails.current_plan_id : 'public',
})





export default compose(
    connect(mapStateToProps, null),
    withStyles(style),
)(ComponentWithAccountType)