import React from "react";
import * as yup from 'yup';
import { Route, Switch } from "react-router-dom";
import { Authentication, ForgotPassword, Login, ResetPassword, ComingSoon } from "../authentication";
import { withRouter } from 'react-router-dom';
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { authenticateActions } from '../../actions';
import CreateAccount from "./createAccount";
import FreeAccount from "./freeAccount"
import FreeAccountVerification from "./freeAccountVerification";

let accountFormSchema = yup.object().shape({
    first_name: yup.string().required(),
    last_name: yup.string().required(),
    email: yup.string().email().required(),
    password: yup.string().required(),
    mobile: yup
        .number()
        .required()
        .positive(),
    birth_date: yup.number()
        .integer()
        .required()
        .positive()
        .min(1)
        .max(31),
    birth_month: yup.number()
        .integer()
        .required()
        .positive()
        .min(1)
        .max(12),
    birth_year: yup.number()
        .integer()
        .required()
        .positive(),
    gender: yup.string().required().oneOf(['male', 'female']),
});

class AuthenticationContainer extends React.Component {
    constructor() {
        super();
        this.state = {
            email: "",
            password: "",
            rememberMe: false,
            freeAccountForm: {
                first_name: '',
                last_name: '',
                email: '',
                password: '',
                mobile: null,
                birth_date: null,
                birth_month: null,
                birth_year: null,
                gender: '',
                invalidLoginFormFields: [],
                validLoginFormFields: [],
            },
            invalidLoginFormFields: [],
            validLoginFormFields: []
        }
    }

    handleInput = event => {
        this.setState({
            [event.target.name]: event.target.value,
        }, () => console.log(this.state))
    }

    handleNewAccountInput = (event, selected) => {
        let target = event.target.name
        let value = event.target.value

        console.log(target, "  :  ", value, selected)

        this.setState(prevState => ({
            freeAccountForm: {
                ...prevState.freeAccountForm,
                [target]: target === 'gender' ? selected : value,
            }
        }), () => console.log(this.state))
    }

    handleNewAccountToggleInput = (event, value) => {
        if (value === "male" || value === "female") {
            this.setState(prevState => ({
                freeAccountForm: {
                    ...prevState.freeAccountForm, "gender": value,
                },
            }));
        }
    }

    handleChange = (event) => {
        let { invalidLoginFormFields, validLoginFormFields } = this.state;
        this.setState({
            [event.target.name]: event.target.value,
        })
        if (event.target.value === "" || event.target.value === null) {
            if (!invalidLoginFormFields.includes(event.target.name)) {
                invalidLoginFormFields.push(event.target.name);
                const index = validLoginFormFields.indexOf(event.target.name);
                if (validLoginFormFields.includes(event.target.name)) {
                    validLoginFormFields.splice(index, 1);
                }
            }
        }
        else {
            const index = invalidLoginFormFields.indexOf(event.target.name);
            if (invalidLoginFormFields.includes(event.target.name)) {
                invalidLoginFormFields.splice(index, 1);
            }
            if (!validLoginFormFields.includes(event.target.name)) {
                validLoginFormFields.push(event.target.name);
            }
        }
        if (event.target.name === "email" && event.target.value !== "") {
            if (!this.validateEmail(event.target.value)) {
                if (!invalidLoginFormFields.includes(event.target.name)) {
                    invalidLoginFormFields.push(event.target.name);
                    const index = validLoginFormFields.indexOf(event.target.name);
                    if (validLoginFormFields.includes(event.target.name)) {
                        validLoginFormFields.splice(index, 1);
                    }
                }
            }
            else {
                const index = invalidLoginFormFields.indexOf(event.target.name);
                if (invalidLoginFormFields.includes(event.target.name)) {
                    invalidLoginFormFields.splice(index, 1);
                }
                if (!validLoginFormFields.includes(event.target.name)) {
                    validLoginFormFields.push(event.target.name);
                }
            }
        }
        if (event.target.name === "password" && event.target.value !== "") {
            // if (!this.validatePassword(event.target.value)) {
            if (!validLoginFormFields.includes(event.target.name)) {
                validLoginFormFields.push(event.target.name);
                const index = invalidLoginFormFields.indexOf(event.target.name);
                if (invalidLoginFormFields.includes(event.target.name)) {
                    invalidLoginFormFields.splice(index, 1);
                }
            }
            // }
            else {
                const index = invalidLoginFormFields.indexOf(event.target.name);
                if (invalidLoginFormFields.includes(event.target.name)) {
                    invalidLoginFormFields.splice(index, 1);
                }
                if (!validLoginFormFields.includes(event.target.name)) {
                    validLoginFormFields.push(event.target.name);
                }
            }
        }
    }

    handleAccountFormChange = (event) => {
        event.preventDefault();
        let target = event.target.name
        let value = event.target.value
        console.log("FORM: ", target, value)

        let { invalidLoginFormFields, validLoginFormFields } = this.state.freeAccountForm;

        if(target === 'mobile'){
            value = value.replace(/\D+/g, '')
        }

        this.setState(prevState => ({
            ...prevState,
            freeAccountForm: {
                ...prevState.freeAccountForm,
                [target]: value,
            }
            
        }), () => console.log("FORM STATE: ", this.state))

        let schema = yup.reach(accountFormSchema, target)

        schema.isValid(value)
            .then(valid => {
                console.log('valid: ', valid)
                if (!valid) {
                    if (!invalidLoginFormFields.includes(target)) {
                        invalidLoginFormFields.push(target);
                        const index = validLoginFormFields.indexOf(target);
                        if (validLoginFormFields.includes(target)) {
                            validLoginFormFields.splice(index, 1);
                        }
                    }
                }
                else {
                    const index = invalidLoginFormFields.indexOf(target);
                    if (invalidLoginFormFields.includes(target)) {
                        invalidLoginFormFields.splice(index, 1);
                    }
                    if (!validLoginFormFields.includes(target)) {
                        validLoginFormFields.push(target);
                    }
                }

                this.setState(prevState => ({
                    freeAccountForm: {
                        ...this.state.freeAccountForm,
                        invalidLoginFormFields,
                        validLoginFormFields,
                    }
                }), console.log("WOOF: ", this.state.freeAccountForm))
            })
            .catch(

            )
    }

    validateEmail(value) {
        const emailRegex = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return emailRegex.test(String(value).toLowerCase());
    }

    validatePassword(value) {
        return value.length >= 8;
    }

    handleLogin = (event) => {
        event.preventDefault();
        let { email, password, invalidLoginFormFields } = this.state;
        // this line in below else after validation is done
        this.props.login({ email: email, password: password });
        const invalidFields = [];
        if (email === "" || email === null) {
            invalidFields.push("email");
        }
        if (password === "" || password === null) {
            invalidFields.push("password");
        }
        invalidLoginFormFields = Object.assign(invalidLoginFormFields, invalidFields);
        if (invalidLoginFormFields.length) {
            this.setState({ invalidLoginFormFields });
            return;
        }
        // else {
        // }

    }

    handleSubmitEmail = () => {
        let { email, invalidLoginFormFields } = this.state;
        const invalidFields = [];
        if (email === "" || email === null) {
            invalidFields.push("email");
        }
        invalidLoginFormFields = Object.assign(invalidLoginFormFields, invalidFields);
        if (invalidLoginFormFields.length) {
            this.setState({ invalidLoginFormFields });
            return;
        } else {
            this.props.submitEmail({ email: email });
        }
    }
    componentDidUpdate(prevProps, prevState) {

    }

    handleSubmitPassword = () => {
        let { password, invalidLoginFormFields, email } = this.state;
        const invalidFields = [];
        if (password === "" || password === null) {
            invalidFields.push("password");
        }
        invalidLoginFormFields = Object.assign(invalidLoginFormFields, invalidFields);
        if (invalidLoginFormFields.length) {
            this.setState({ invalidLoginFormFields });
            return;
        } else {
            this.props.submitPassword({ email: email, password: password });
        }
    }
    render() {
        const { match, loginStatus, forgotPassword, resetPassword, loginType } = this.props;
        const { email, password, freeAccountForm, validLoginFormFields, invalidLoginFormFields } = this.state;
        console.log("MATCH: ", match.url)
        console.log("LOGIN TYPE: ", loginType)
        return (
            <Switch>
                { /* loginType === 'listingModal' &&
                    <Route exact path={`${match.url}/`} render={() => <CreateAccount email={email} url={match.url} loginStatus={loginStatus} invalidLoginFormFields={invalidLoginFormFields} validLoginFormFields={validLoginFormFields}
                        password={password} handleChange={this.handleChange} handleInput={this.handleInput} handleLogin={this.handleLogin} loginType={loginType} />} /> */
                }
                <Route exact path={`${match.url}`} render={() => {
                    return  ['listingModal', 'referredListing'].includes(loginType) ? 
                        <CreateAccount formData={freeAccountForm} url={match.url} loginStatus={loginStatus} invalidLoginFormFields={freeAccountForm.invalidLoginFormFields} validLoginFormFields={freeAccountForm.validLoginFormFields}
                            handleChange={this.handleAccountFormChange} handleInput={this.handleNewAccountInput} handleToggle={this.handleNewAccountToggleInput} handleLogin={this.handleLogin} loginType={loginType} /> :
                        <Authentication url={match.url} loginType={loginType} />
                }} />
                <Route exact path={`${match.url}/login-buyer`} render={() => <Login email={email} url={match.url} loginStatus={loginStatus} invalidLoginFormFields={invalidLoginFormFields} validLoginFormFields={validLoginFormFields}
                    password={password} handleChange={this.handleChange} handleInput={this.handleInput} handleLogin={this.handleLogin} loginType={loginType} />} />
                <Route exact path={`${match.url}/create-account`} render={() => <CreateAccount formData={freeAccountForm} url={match.url} loginStatus={loginStatus} invalidLoginFormFields={freeAccountForm.invalidLoginFormFields} validLoginFormFields={freeAccountForm.validLoginFormFields}
                            handleChange={this.handleAccountFormChange} handleInput={this.handleNewAccountInput} handleToggle={this.handleNewAccountToggleInput} handleLogin={this.handleLogin} loginType={loginType} />} />
                {/* <Route exact path={`${match.url}/create-account/free-account-form`} render={() => <FreeAccount formData={freeAccountForm} url={match.url} loginStatus={loginStatus} invalidLoginFormFields={invalidLoginFormFields} validLoginFormFields={validLoginFormFields}
                    handleChange={this.handleChange} handleInput={this.handleInput} handleLogin={this.handleLogin} loginType={loginType} />} /> */}
                <Route exact path={`${match.url}/free-account-verification`} render={() => <FreeAccountVerification email={email} url={match.url} loginStatus={loginStatus} invalidLoginFormFields={invalidLoginFormFields} validLoginFormFields={validLoginFormFields}
                    password={password} handleChange={this.handleChange} handleInput={this.handleInput} handleLogin={this.handleLogin} loginType={loginType} />} />
                <Route exact path={`${match.url}/forgot-password`} render={() => <ForgotPassword email={email} forgotPassword={forgotPassword}
                    handleChange={this.handleChange} handleInput={this.handleInput} handleSubmitEmail={this.handleSubmitEmail} invalidLoginFormFields={invalidLoginFormFields} validLoginFormFields={validLoginFormFields} loginType={loginType} />} />
                <Route exact path={`${match.url}/reset-password`} render={() => <ResetPassword password={password} handleChange={this.handleChange}
                    handleSubmitPassword={this.handleSubmitPassword} resetPassword={resetPassword} loginType={loginType} />} />
                {/* <Route exact path={`${match.url}/coming-soon`} render={() => <ComingSoon loginType={loginType} />} /> */}
                {/* <Route component={ErrorPage} /> */}
            </Switch>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        ...state.authenticateReducer,
        loginType: state.propertyReducer.loginType
    }
};

const mapDispatchToProps = (dispatch) => {
    return {
        ...bindActionCreators(authenticateActions, dispatch)
    }
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(AuthenticationContainer));