import authenticationContainer from "../components/authentication/authenticationContainer";
import { staticData } from "../common";

const initialState = {
    pagination: {
        pageNumber: 1,
        limit: 12,
        totalCount: 0,
        order_by: 0,
    },
    isFetchingListings: false,
    filters: {},
    showEmailSuccess: false,
    showEmailForm: false,
    showSuccessRefer: true,
    isFavListingRemoved: false,
    listings: [],
    favouriteListings: [],
    isAddingFavourite: [],
}
export default function propertyReducer(state = JSON.parse(JSON.stringify(initialState)),
    action) {
    switch (action.type) {
        case "IS_FETCHING_LISTINGS":
            return { ...state, isFetchingListings: action.status }
        case "IS_ADDING_FAVOURITE":
            console.log("SPLICE: ", action.status, action.id)
            console.log("SPLICE: ", state.isAddingFavourite.indexOf(action.id))
            let splicedArray = state.isAddingFavourite.splice(state.isAddingFavourite.indexOf(action.id), 1)
            console.log("SPLICED: ", splicedArray)
            let newArray = action.status === true ? [...state.isAddingFavourite, action.id] : splicedArray
            return  { ...state, isAddingFavourite: newArray }
        case "GET_PROPERTY_DETAILS":
            return { ...state, propertyDetails: action.payload }
        case "LISTINGS_FETCHED":
            return Object.assign({}, state,
                { listings: action.payload.listings });
        case "SEARCH_FILTER":
            var filters = state.filters;
            
                if (action.payload.name === 'Asset Type') {
                    delete filters["Property Type"];
                }
                filters[action.payload.name] = action.payload.data;
            
            return { ...state, filters }
        case "PAGINATION":
            return { ...state, pagination: action.payload }
        case "SORT_COLUMN":
            return { ...state, filters: { ...state.filters, order_by: action.payload }}
        case "SET_FETCHING_FLAG":
        case "RESET_PAGE_OFFSET":
        case "RESET_FILTER_FLAG":
        case "SET_SORT_COLUMN":
            return state;
        case "CLEAR_FILTER":
            var filters = state.filters;
            delete filters[action.payload];
            return { ...state, filters }
        case "CLEAR_ALL_FILTERS":
            return { ...state, filters: {} }
        case "FAVOURITE_LISTINGS":
            return { ...state, favouriteListings: action.payload }
        case "RESET_PAGINATION":
            var pagination = state.pagination;
            pagination.pageNumber = 1;
            pagination.totalCount = 0;
            return { ...state, pagination: pagination }
        case "CHANGE_EMAIL_SUCCESS":
            return { ...state, showEmailSuccess: !state.showEmailSuccess }
        case "SHOW_EMAIL_FORM":
            return { ...state, showEmailForm: !state.showEmailForm }
        case "SHOW_SUCCESS_REFER":
            return { ...state, showSuccessRefer: !state.showSuccessRefer }
        case "SET_LOGIN_TYPE":
            return { ...state, loginType: action.payload }
        case "FAV_LISTING_REMOVED":
            return { ...state, isFavListingRemoved: action.payload }
        default:
            return state;
    }
}