const Sucess = (props) => {
    const { classes, isAgentOnboarding } = props;
    console.log(isAgentOnboarding);

    const getClasses = (status) => {
        if (props.isAdminOnboarding) {
            return props.classes[`admin${status}`]
        }
        else if (props.isAgentOnboarding) {
            return props.classes[`agent${status}`]
        }
        else if (props.isBloggerOnboarding) {
            return props.classes[`blogger${status}`]
        }
    }

    return (
        <div>
            <Grid container
                direction="row"
                justify="center"
                alignItems="center"
                className={classes.containerMain}>
                <Grid item xs={12} className={getClasses('FormHeadingSuccess')}>
                    <Typography noWrap>
                        WELL DONE!
                    </Typography>
                </Grid>
                <Grid item xs={12}>
                    <Typography className={classes.successMessageIcon}>
                        <i className="sb-icon-clap"></i>
                    </Typography>
                    <Typography className={classes.successMessage}>
                        Thank you for completing <br />the Admin Onboarding
                    </Typography>
                    <Typography className={classes.successMessage}>
                        The Admin Office will be<br /> in touch with you shortly<br /> after verifying your account
                    </Typography>

                </Grid>
                <Grid item xs={12} className={classes.sellerBailoutLogo}>
                    <Typography noWrap>
                        Sincerely,
                    </Typography>
                    <img src={Logo} alt="SellUp" title="SellUp" />
                </Grid>
            </Grid>
            <Grid container
                direction="row"
                justify="center"
                alignItems="center"
                className={classes.containerButton}>

                <Link to={`${process.env.PUBLIC_URL}/`} color="warning" className={getClasses('ButtonSecondry')}>GO TO HOMEPAGE</Link>
            </Grid>
        </div>
    )
}

export default withStyles(styles)(Sucess);