import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import PropTypes from 'prop-types';
import { Grid, Stepper, Step, StepLabel, StepConnector, Typography } from '@material-ui/core';
import Button from "../uiComponent/CustomButtons/Button";
import { staticData } from "../../common";
import classNames from 'classnames';
import { Info, Preferences, DirectPay, Sucess, Upload } from ".";
import { validateAgentFormData, validateBuyerInfo, validateBuyerProfile } from './validation';
import { onboardingActions } from '../../actions';
import { connect } from "react-redux";
import { bindActionCreators } from "redux";

import onBoardingStyle from "./onBoardingStyle";
import { sanitizeText } from '../common/utils';

function getStepContent(step, isAgentOnboarding, isAdminOnboarding, isBloggerOnboarding, handleChange, formData, invalidBuyerFormFields, handleInput, handlButtonToggle, validBuyerFormFields, handleDoneClick_buyerInfo, handleDoneClick_buyerProfile, setBuyerInfoValidated, handleFormEdit, handleNext, setBuyerProfileValidated) {
  console.log("STEP: ", step)
  switch (step) {
    case 0:
      return (
        <Info isAgentOnboarding={isAgentOnboarding} isAdminOnboarding={isAdminOnboarding} isBloggerOnboarding={isBloggerOnboarding} handleChange={handleChange} handleInput={handleInput} handlButtonToggle={handlButtonToggle}
          formData={formData} invalidBuyerFormFields={invalidBuyerFormFields} validBuyerFormFields={validBuyerFormFields}
          handleDoneClick_buyerInfo={handleDoneClick_buyerInfo} setBuyerInfoValidated={setBuyerInfoValidated} />
      );
    case 1:
      return (
        <Preferences handleChange={handleChange} handleInput={handleInput}
          formData={formData} invalidBuyerFormFields={invalidBuyerFormFields} validBuyerFormFields={validBuyerFormFields}
          setBuyerProfileValidated={setBuyerProfileValidated} handleDoneClick_buyerProfile={handleDoneClick_buyerProfile} />
      );
    case 2:
      return (
        <DirectPay handleChange={handleChange} handleInput={handleInput}
          formData={formData} invalidBuyerFormFields={invalidBuyerFormFields} validBuyerFormFields={validBuyerFormFields}
          setBuyerProfileValidated={setBuyerProfileValidated} handleDoneClick_buyerProfile={handleDoneClick_buyerProfile} />
      );
    case 3:
      return (
        <Upload isAgentOnboarding={isAgentOnboarding} isAdminOnboarding={isAdminOnboarding} isBloggerOnboarding={isBloggerOnboarding} handleChange={handleChange}
          formData={formData} invalidBuyerFormFields={invalidBuyerFormFields} handleFormEdit={handleFormEdit} handleNext={handleNext} />
      );
    default:
      return (
        <Sucess isAgentOnboarding={isAgentOnboarding} isAdminOnboarding={isAdminOnboarding} isBloggerOnboarding={isBloggerOnboarding} />
      );
  }
}

class OnboardingForm extends React.Component {
  state = {
    activeStep: 0,
    stepCount: 0,
    skipped: new Set(),
    agentOnBoardFormData: staticData.agentOnBoardFormData,
    completedSteps: [],
    invalidBuyerFormFields: [],
    validBuyerFormFields: [],
    buyerInfoValidated: false,
    buyerProfileValidated: false
  };

  isStepOptional = step => {
    return step === 1;
  };

  handleNext = () => {
    let steps = this.getSteps()

    let { activeStep, stepCount, completedSteps, invalidBuyerFormFields } = this.state;
    let { isAgentOnboarding, isAdminOnboarding, isBloggerOnboarding } = this.props

    let currentStep = steps.findIndex(step => step.id == this.state.activeStep)

    invalidBuyerFormFields = [];
    const invalidFields = validateAgentFormData(activeStep, this.state.agentOnBoardFormData, { isAgentOnboarding, isAdminOnboarding, isBloggerOnboarding });
    invalidBuyerFormFields = Object.assign(invalidBuyerFormFields, invalidFields);
    if (invalidBuyerFormFields.length) {
      this.setState({ invalidBuyerFormFields });
      return;
    }
    completedSteps.push(stepCount);
    console.log("Completed Steps: ", completedSteps)
    let { skipped } = this.state;
    if (this.isStepSkipped(activeStep)) {
      skipped = new Set(skipped.values());
      skipped.delete(activeStep);
    }
    this.setState(state => ({
      activeStep: steps[currentStep + 1].id,
      stepCount: state.stepCount + 1,
      skipped,
      completedSteps
    }), () => console.log("Completed Steps==> ", this.state.completedSteps, this.state.activeStep));
    window.scrollTo(0, 0);
    if (completedSteps.length === 4) {
      // this.props.agentOnboarding(this.state.agentOnBoardFormData);
    }
  };

  // handleNext = () => {
  //   let { activeStep, completedSteps, invalidBuyerFormFields } = this.state;
  //   invalidBuyerFormFields = [];
  //   const invalidFields = validateAgentFormData(activeStep, this.state.agentOnBoardFormData);
  //   invalidBuyerFormFields = Object.assign(invalidBuyerFormFields, invalidFields);
  //   if (invalidBuyerFormFields.length) {
  //     this.setState({ invalidBuyerFormFields });
  //     return;
  //   }
  //   completedSteps.push(activeStep);
  //   let { skipped } = this.state;
  //   if (this.isStepSkipped(activeStep)) {
  //     skipped = new Set(skipped.values());
  //     skipped.delete(activeStep);
  //   }
  //   this.setState({
  //     activeStep: activeStep + 1,
  //     skipped,
  //     completedSteps
  //   });
  //   window.scrollTo(0, 0);
  //   if(completedSteps.length === 4){
  //     // this.props.agentOnboarding(this.state.agentOnBoardFormData);
  //   }
  // };

  handleDoneClick_buyerInfo = (stepName) => {
    let { invalidBuyerFormFields } = this.state;
    const invalidFields = validateBuyerInfo(stepName, this.state.agentOnBoardFormData);
    invalidBuyerFormFields = Object.assign(invalidBuyerFormFields, invalidFields);
    if (invalidBuyerFormFields.length) {
      this.setState({ invalidBuyerFormFields });
      return;
    }
    return true;
  };

  handleDoneClick_buyerProfile = (stepName) => {
    let { invalidBuyerFormFields } = this.state;
    const invalidFields = validateBuyerProfile(stepName, this.state.agentOnBoardFormData);
    invalidBuyerFormFields = Object.assign(invalidBuyerFormFields, invalidFields);
    if (invalidBuyerFormFields.length) {
      this.setState({ invalidBuyerFormFields });
      return;
    }
    return true;
  }

  setBuyerInfoValidated = () => {
    this.setState({ buyerInfoValidated: true })
  }

  setBuyerProfileValidated = () => {
    this.setState({ buyerProfileValidated: true })
  };

  handlButtonToggle = (event, value) => {
    if (value === "MALE" || value === "FEMALE") {
      this.setState({
        agentOnBoardFormData: {
          ...this.state.agentOnBoardFormData, "gender": value,
        },
      });
    } else {
      this.setState({
        agentOnBoardFormData: {
          ...this.state.agentOnBoardFormData, "field-matches-drivers-license": value,
        },
      });
    }
  }

  handleBack = () => {
    let steps = this.getSteps()

    const { activeStep, completedSteps } = this.state;

    let currentStep = steps.findIndex(step => step.id == this.state.activeStep)

    completedSteps.splice(completedSteps.indexOf(activeStep - 1))
    this.setState(state => ({
      activeStep: steps[currentStep - 1].id,
      stepCount: state.stepCount - 1,
      completedSteps,
      invalidBuyerFormFields: []
    }));
    window.scrollTo(0, 0);
  };

  handleReset = () => {
    this.setState({
      activeStep: 0,
      stepCount: 0,
      completedSteps: []
    });
  };

  isStepSkipped(step) {
    return this.state.skipped.has(step);
  }

  handleInput = event => {
    if ((event.target.name === "field-directPay-zip" || event.target.name === "field-zip" || event.target.name === "field-routing-number" || event.target.name === "field-account-number"
      || event.target.name === "birth-month" || event.target.name === "birth-date" || event.target.name === "birth-year") && (isNaN(event.target.value))) {
      return;
    }
    this.setState({
      agentOnBoardFormData: {
        ...this.state.agentOnBoardFormData, [event.target.name]: event.target.value,
      },
    });
  }

  handleChange = event => {
    let name = event.target.name
    let value = sanitizeText(name, event.target.value)

    let { invalidBuyerFormFields, validBuyerFormFields } = this.state;
    this.setState({
      agentOnBoardFormData: {
        ...this.state.agentOnBoardFormData, [name]: value,
      },
    });
    if (value === "" || value === null) {
      if (!invalidBuyerFormFields.includes(name)) {
        invalidBuyerFormFields.push(name);
        const index = validBuyerFormFields.indexOf(name);
        if (validBuyerFormFields.includes(name)) {
          validBuyerFormFields.splice(index, 1);
        }
      }
    }
    else {
      const index = invalidBuyerFormFields.indexOf(name);
      if (invalidBuyerFormFields.includes(name)) {
        invalidBuyerFormFields.splice(index, 1);
      }
      if (!validBuyerFormFields.includes(name)) {
        validBuyerFormFields.push(name);
      }
    }
    if (name === "field-home-email" && value !== "") {
      if (!this.validateEmail(value)) {
        if (!invalidBuyerFormFields.includes(name)) {
          invalidBuyerFormFields.push(name);
          const index = validBuyerFormFields.indexOf(name);
          if (validBuyerFormFields.includes(name)) {
            validBuyerFormFields.splice(index, 1);
          }
        }
      }
      else {
        const index = invalidBuyerFormFields.indexOf(name);
        if (invalidBuyerFormFields.includes(name)) {
          invalidBuyerFormFields.splice(index, 1);
        }
        if (!validBuyerFormFields.includes(name)) {
          validBuyerFormFields.push(name);
        }
      }
    }
    if ((name === "field-directPay-zip" || name === "field-zip") && value !== "") {
      if (!this.validateZip(value)) {
        if (!invalidBuyerFormFields.includes(name)) {
          invalidBuyerFormFields.push(name);
          const index = validBuyerFormFields.indexOf(name);
          if (validBuyerFormFields.includes(name)) {
            validBuyerFormFields.splice(index, 1);
          }
        }
      }
      else {
        const index = invalidBuyerFormFields.indexOf(name);
        if (invalidBuyerFormFields.includes(name)) {
          invalidBuyerFormFields.splice(index, 1);
        }
        if (!validBuyerFormFields.includes(name)) {
          validBuyerFormFields.push(name);
        }
      }
    }
    if (name === "cvc" && value !== "") {
      if (!this.validateCvc(value)) {
        if (!invalidBuyerFormFields.includes(name)) {
          invalidBuyerFormFields.push(name);
          const index = validBuyerFormFields.indexOf(name);
          if (validBuyerFormFields.includes(name)) {
            validBuyerFormFields.splice(index, 1);
          }
        }
      }
      else {
        const index = invalidBuyerFormFields.indexOf(name);
        if (invalidBuyerFormFields.includes(name)) {
          invalidBuyerFormFields.splice(index, 1);
        }
        if (!validBuyerFormFields.includes(name)) {
          validBuyerFormFields.push(name);
        }
      }
    }
    if ((name === "work_phone" || name === "phone") &&
      value !== "") {
      const number = value.replace(/[^0-9.]/g, "");
      if (number.toString().length < 10) {
        if (!invalidBuyerFormFields.includes(name)) {
          invalidBuyerFormFields.push(name);
          const index = validBuyerFormFields.indexOf(name);
          if (validBuyerFormFields.includes(name)) {
            validBuyerFormFields.splice(index, 1);
          }
        }
      }
      else {
        const index = invalidBuyerFormFields.indexOf(name);
        if (invalidBuyerFormFields.includes(name)) {
          invalidBuyerFormFields.splice(index, 1);
        }
        if (!validBuyerFormFields.includes(name)) {
          validBuyerFormFields.push(name);
        }
      }
    }
    if (name === "password_confirmation" && value !== "") {
      if (!this.matchPassword(value)) {
        if (!invalidBuyerFormFields.includes(name)) {
          invalidBuyerFormFields.push(name);
          const index = validBuyerFormFields.indexOf(name);
          if (validBuyerFormFields.includes(name)) {
            validBuyerFormFields.splice(index, 1);
          }
        }
      }
      else {
        const index = invalidBuyerFormFields.indexOf(name);
        if (invalidBuyerFormFields.includes(name)) {
          invalidBuyerFormFields.splice(index, 1);
        }
        if (!validBuyerFormFields.includes(name)) {
          validBuyerFormFields.push(name);
        }
      }
    }
    this.setState({
      invalidBuyerFormFields: invalidBuyerFormFields,
      validBuyerFormFields: validBuyerFormFields
    });
  };

  handleFormEdit = (stepIndex) => {
    this.setState({ activeStep: stepIndex })
  };

  validateEmail(value) {
    const emailRegex = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return emailRegex.test(String(value).toLowerCase());
  };

  matchPassword(value) {
    return value === this.state.agentOnBoardFormData.password;
  }

  validateZip(value) {
    const zipRegex = /^[0-9]*$/;
    return zipRegex.test(String(value).toLowerCase()) && value.length === 5;
  };

  validateCvc(value) {
    const cvcRegex = /^[0-9]{3,4}$/;
    return cvcRegex.test(String(value).toLowerCase());
  };

  getStepIcon = (step, stepIndex) => {
    const icon1 = this.props.isAgentOnboarding ? <i className="sb-icon-agent"></i> : this.props.isAdminOnboarding ? <i className="sb-icon-admin"></i> : <i className="sb-icon-blogger"></i>
    const icon2 = <i className="sb-icon-flag"></i>
    const icon3 = <i className="sb-icon-bank"></i>
    const icon4 = <i className="sb-icon-upload"></i>
    const stepCompletedIcon = <i className="sb-icon-tick-bold"></i>

    if (this.state.completedSteps.indexOf(stepIndex) > -1) {
      return stepCompletedIcon;
    }

    switch (step.step) {
      case "ADMIN":
      case "AGENT":
      case "BLOGGER":
        return icon1;
      case "PREFERENCES": return icon2;
      case "DIRECT PAY": return icon3;
      case "UPLOAD": return icon4;
      default:
    }
  };

  isValidStep = () => {
    if (this.state.activeStep === 0) {
      return this.state.agentOnBoardFormData.plan;
    }
    else if (window.SB_IsMobileView) {
      if (this.state.activeStep === 1) {
        return this.state.buyerInfoValidated;
      }
      else if (this.state.activeStep === 2) {
        return this.state.buyerProfileValidated;
      }
    }
    return true;
  };

  getFormClass = () => {
    if (this.props.isAdminOnboarding) {
      return this.props.classes.stepperAdmin
    }
    else if (this.props.isAgentOnboarding) {
      return this.props.classes.stepperAgent
    }
    else if (this.props.isBloggerOnboarding) {
      return this.props.classes.stepperBlogger
    }
  }
  getClasses = (status) => {
    if (this.props.isAdminOnboarding) {
      return this.props.classes[`admin${status}`]
    }
    else if (this.props.isAgentOnboarding) {
      return this.props.classes[`agent${status}`]
    }
    else if (this.props.isBloggerOnboarding) {
      return this.props.classes[`blogger${status}`]
    }
  }
  getSteps = () => {
    if (this.props.isAdminOnboarding) {
      return staticData.adminFormSteps
    }
    else if (this.props.isAgentOnboarding) {
      return staticData.agentFormSteps
    }
    else if (this.props.isBloggerOnboarding) {
      return staticData.bloggerFormSteps
    }
  }
  getTitle = () => {
    if (this.props.isAdminOnboarding) {
      return "Admin"
    }
    else if (this.props.isAgentOnboarding) {
      return "Agent"
    }
    else if (this.props.isBloggerOnboarding) {
      return "Blogger"
    }
  }

  render() {
    let steps = this.getSteps();
    steps = steps.slice(0, steps.length - 1);
    // let SB_IsMobileView = window.matchMedia("(max-width: 767px)").matches;
    const { activeStep, stepCount, agentOnBoardFormData, invalidBuyerFormFields } = this.state;
    const { classes, isAgentOnboarding, isAdminOnboarding, isBloggerOnboarding } = this.props;
    const connector = (
      <StepConnector
        classes={{
          active: this.getClasses('ConnectorActive'),
          completed: this.getClasses('ConnectorCompleted'),
          disabled: this.getClasses('ConnectorDisabled'),
          line: this.getClasses('ConnectorLine'),
        }}
      />
    );

    return (
      <Grid className={classes.container} container direction="row" justify="center" alignItems="flex-start">
        <Grid item xs={12} className={'form-wrapper'}>
          <div className={classes.stepperHead}>
            <Typography variant="h5" align='center'>{/* {this.props.isAgentOnboarding ? 'Agent' : 'Team'} */}{this.getTitle()} Onboarding.</Typography>
            <Stepper activeStep={stepCount} className={this.getFormClass()} alternativeLabel connector={connector}>
              {steps.map((step, index) => {
                const props = {};
                const labelProps = {};
                return (
                  <Step key={step} {...props}>
                    <StepLabel
                      className={this.getClasses('StepperLabel')}
                      classes={{
                        active: classes.stepperActive,
                        completed: classes.stepperCompleted,
                        disabled: classes.stepperDisabled,
                        line: classes.stepperLine,
                      }}
                      icon={this.getStepIcon(step, index)}{...labelProps}>{step.step}
                    </StepLabel>
                  </Step>
                );
              })}
            </Stepper>
          </div>

          <Grid item xs={12}>
            {stepCount === steps.length ? (
              <Grid item xs={12}>
                <Sucess isAgentOnboarding={isAgentOnboarding} isAdminOnboarding={isAdminOnboarding} isBloggerOnboarding={isBloggerOnboarding} />
              </Grid>
            ) : (
                <Grid item xs={12}>
                  <Grid xs={12} item>
                    {getStepContent(activeStep, this.props.isAgentOnboarding, this.props.isAdminOnboarding, this.props.isBloggerOnboarding, this.handleChange, agentOnBoardFormData, invalidBuyerFormFields, this.handleInput, this.handlButtonToggle, this.state.validBuyerFormFields, this.handleDoneClick_buyerInfo, this.handleDoneClick_buyerProfile, this.setBuyerInfoValidated, this.handleFormEdit, this.handleNext, this.setBuyerProfileValidated)}
                  </Grid>

                  <Grid
                    container
                    direction="row"
                    justify={`${window.SB_IsMobileView ? (activeStep === 0 ? "center" : "space-between")
                      : (activeStep === 0 ? "flex-end" : "space-between")}`}
                    alignItems="center"
                  >
                    {activeStep !== 0 && (
                      <Button
                        simple
                        onClick={this.handleBack}
                        className={this.getClasses("ButtonSecondryTrans")}
                      >
                        Back
                    </Button>)}

                    <Button
                      color="primary"
                      size="lg"
                      // disabled={!this.isValidStep()}
                      onClick={this.handleNext}
                      className={this.getClasses("ButtonSecondry")}
                    >
                      {stepCount === steps.length - 1 ? 'UPLOAD' : 'NEXT'}
                    </Button>
                  </Grid>
                  <Grid
                    container
                    direction="row"
                    justify='center'
                    alignItems="center"
                    className={classNames(classes.notification)}
                  >
                  </Grid>
                </Grid>
              )}
          </Grid>
        </Grid>
      </Grid>

    );
  }
}

OnboardingForm.propTypes = {
  classes: PropTypes.object.isRequired,
};

const mapDispatchToProps = (dispatch) => {
  return {
    ...bindActionCreators({ ...onboardingActions }, dispatch)
  }
};

export default connect(mapDispatchToProps)(withStyles(onBoardingStyle)(OnboardingForm));