import React from 'react'
import compose from 'recompose/compose'
import { connect } from 'react-redux'
import { withStyles } from '@material-ui/core'

let style = theme => ({
    disabledLink: {
        '&:hover': {
            color: '#bebebe',
            cursor: 'not-allowed',
        }
    },
    blurBadge: {
        filter: "blur(2px)",
    },
    blurImg: {
        filter: 'blur(10px)',
    },
    blurPara: {
        filter: "blur(3px)",
    },
    blurHeading: {
        filter: "blur(10px)",
    },
    blurDiv: {
        '& p, & a, & i': {
            filter: "blur(3px)",
        },
        '& h1, & h2, & h3': {
            filter: "blur(5px)",
        },
        '& h4, & h5, & h6': {
            filter: "blur(3px)",
        },
        '& img': {
            filter: "blur(5px)",
        },
        '& a:hover': {
            textDecoration: 'none',
            cursor: 'default',
        },
        '& button .MuiButton-label': {
            // background: '',
            filter: "blur(3px)",
        },
        '& button:hover': {
            // background: '',
            cursor: 'default',
        }
    }
})

const AccountTypeHOC = (Component) => {
    class ComponentWithAccountType extends React.Component{
        constructor(props){
            super(props)
        }
    
        getAccountType = () => {
            return this.props.accountType
        }

        getRestrictionClass = (classname) => {
            const classes = this.props.classes
            
            if(this.props.accountType === 'public'){
                return classes[classname]
            }
            else{
                return ''
            }
        }
    
        getDisabled = (condition) => {
            if(typeof condition === 'string')
                return this.props.accountType === condition
            else if(Array.isArray(condition))
                return condition.includes(this.props.accountType)
        }

        getIsHigherPlan = (plan) => {
            let myPlan = this.getAccountType()

            if(myPlan === 'public'){
                return !['public', 'Public'].includes(plan)
            }
            else if(myPlan === 'free'){
                return !['public', 'Public', 'free'].includes(plan)
            }
            else if(myPlan === 'Basic, basic-investor-monthly' || myPlan === 'basic-investor-yearly'){
                return !['public', 'Public', 'free', 'basic-investor-monthly', 'basic-investor-yearly'].includes(plan)
            }
            else if(myPlan === 'preferred-investor-monthly' || myPlan === 'preferred-investor-yearly'){
                return !['public', 'Public', 'free', 'Basic', 'basic-investor-monthly', 'basic-investor-yearly',
                        'Preferred', 'preferred-investor-monthly', 'preferred-investor-yearly'].includes(plan)
            }
            else if(myPlan === 'first-mover-investor-yearly' || myPlan === 'first-mover-investor-monthly'){
                return !['public', 'Public', 'free', 'Basic', 'basic-investor-monthly', 'basic-investor-yearly',
                        'Preferred', 'preferred-investor-monthly', 'preferred-investor-yearly', 'First Mover',
                        'first-mover-investor-monthly', 'first-mover-investor-yearly'].includes(plan)
            }
        }
    
        render(){
            return (
                <Component 
                    {...this.props}
                    getRestrictionClass={this.getRestrictionClass}
                    getDisabled={this.getDisabled}
                    getIsHigherPlan={this.getIsHigherPlan}
                    accountType={this.getAccountType()} />
            )
        }
    }
     
    const mapStateToProps = (state) => ({
        accountType: state.myAccountReducer.buyerProfileDetails ? state.myAccountReducer.buyerProfileDetails.current_plan_id : 'public',
    })

    return compose(
        connect(mapStateToProps, null),
        withStyles(style),
    )(ComponentWithAccountType)
}

export default AccountTypeHOC