import React, { Component } from "react";
import CopyRightFooter from "../footer/copyRightFooter";
import SocialFooter from  "../footer/socialFooter";

const FooterHOC = (FullFooter) => {
    return class FooterHOC extends Component {  
        render() {
            if(window.location.pathname === "/agent-onboarding" || window.location.pathname === "/team-onboarding" || window.location.pathname === "/admin-onboarding"
             || window.location.pathname === "/blogger-onboarding") {
                return null;
            }
           else if(window.location.pathname === "/user-login/agent-login" || window.location.pathname === "/user-login/admin-login"
           || window.location.pathname === "/user-login/blogger-login"
           ) {
                return <CopyRightFooter />;
            }
            return <FullFooter {...this.props}/>
        }
    }
}

export default FooterHOC;